.title {
  margin: 0;

  &--main {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: $f-primary-caps;
    font-size: 36px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: $c-text;
    font-weight: normal;

    .icon {
      flex-shrink: 0;
      position: relative;
      bottom: 2px;
    }

    span {
      margin: 0 10px;
    }

    .icon--title-left,
    .icon--title-right {
      width: 39px;
      height: 36px;
    }

    @include media(">=tablet") {
      font-size: 60px;

      span {
        margin: 0 15px;
      }

      .icon--title-left,
      .icon--title-right {
        width: 54px;
        height: 50px;
      }
    }

    @include media(">desktop") {
      font-size: 72px;
      letter-spacing: 3px;

      .icon--title-left,
      .icon--title-right {
        width: 78px;
        height: 36px;
      }
    }
  }
}
