.flycloud {
  animation: fly 6s infinite;
  animation-timing-function: linear;

  &--delay1 {
    animation-delay: .1s;
  }

  &--delay2 {
    animation-delay: .2s;
  }
}

@keyframes fly {
  0% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(5px, 5px);
  }
  50% {
    transform: translate(0, 10px);
  }
  75% {
    transform: translate(-5px, 5px);
  }
  100% {
    transform: translate(0, 0);
  }
}
