.footer {
  background-color: #fff;
  padding: 30px 0;
  margin-bottom: 10px;

  @include media(">=tablet"){
    padding: 50px 0;
    margin-bottom: 0;
  }

  &__container {
    @include media(">desktop") {
      display: flex;
      justify-content: space-between;
    }
  }

  &__text {
    font-family: $f-secondary;
    font-size: 12px;
    letter-spacing: 0.3px;
    margin-bottom: 30px;
    text-align: center;

    a {
      color: currentColor;
      text-decoration: underline;
    }

    @include media(">=tablet"){
      font-size: 14px;
      line-height: 1.5;
    }

    @include media(">desktop") {
      text-align: left;
      padding-right: 50px;
      margin-bottom: 0;
    }
  }

  &__info {
    line-height: 1;
    text-align: center;

    @include media(">desktop") {
      text-align: right;
      flex-shrink: 0;
    }
  }

  &__info-wiselytext {
    text-transform: uppercase;
    font-size: 29px;
    letter-spacing: 1px;
    font-family: $f-primary-caps;

    @include media(">=tablet"){
      font-size: 34px;
    }
  }

  &__info-wiselylink {
    letter-spacing: 1.5px;
    font-size: 22px;
    color: $c-text;

    a {
      color: currentColor;
      text-decoration: none;

      @include media(">desktop"){
        transition: .3s ease color;

        &:hover {
          color: $c-text-hover;
        }
      }
    }

    @include media(">=tablet"){
      font-size: 26px;
    }
  }

  &__info-icons {
    margin-top: 10px;

    @include media(">=tablet"){
      .icon--drink-wisely {
        width: 120px;
        height: 43px;
      }
    }
  }
}
