.ground {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: relative;
  height: 275px;

  @include media(">desktop") {
    height: 350px;
  }

  &__bg {
    $offset: 50px;

    position: absolute;
    top: 0;
    bottom: 0;
    left: -$offset;
    width: calc(100% + #{$offset});

    max-width: none;
    object-fit: cover;
    height: 100%;

    @include media(">desktop") {
      $offset: 10px;

      left: -$offset;
      width: calc(100% + #{$offset});
    }
  }

  &__claim {
    font-family: $f-primary-caps;
    text-align: center;
    font-size: 26px;
    margin-bottom: 60px;
    position: relative;
    z-index: 5;

    @include media(">=tablet"){
      font-size: 32px;
      letter-spacing: 1px;
    }

    @include media(">desktop") {
      font-size: 36px;
      margin-bottom: 95px;
    }
  }
}
