.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;

  @include media(">desktop") {
    margin-top: 45px;
    max-width: 650px;
    margin: 45px auto 0;
  }

  &__item {
    text-align: center;
    font-family: $f-primary-caps;
    height: 26px;
    color: $c-text;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 1.1;
    width: calc(50% - 1px);

    &-subtitle {
      text-transform: none;
      font-family: $f-primary;
      font-size: 14px;
    }

    @include media(">=tablet") {
      font-size: 20px;

      &-subtitle {
        font-size: 18px;
      }
    }

    @include media(">desktop") {
      font-size: 26px;
      transition: .3s ease color;

      &-subtitle {
        font-size: 24px;
        letter-spacing: 2px;
      }

      &:hover {
        color: $c-text-hover;
      }
    }
  }

  &__separator {
    width: 2px;
    height: 26px;
    background-color: $c-text;
    margin: 0;
    display: inline-block;
    pointer-events: none;
    position: relative;
    bottom: 10px;

    @include media(">=tablet") {
      margin: 0 25px;
      bottom: 0;
    }

    @include media(">desktop") {
      margin: 0 40px;
      height: 34px;
    }
  }

}
