.story {

  &__header {
    margin: 40px 0;

    @include media(">=tablet") {
      margin: 150px 0 40px;
    }

    @include media(">desktop"){
      margin-bottom: 100px;
    }
  }

  &__wrapper {
    position: relative;
  }

  &__main {
    text-align: center;
    font-family: $f-primary;
    font-size: 18px;
    line-height: 1.8;
    letter-spacing: 0.5px;

    @include media(">=tablet") {
      font-size: 20px;
    }

    @include media(">desktop"){
      font-size: 21px;
      letter-spacing: 1px;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
    }
  }

  &__main-container {
    @include media(">desktop"){
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
    }
  }

  &__main-col {
    @include media(">desktop"){
      width: 370px;

      &:first-child {
        text-align: left;
      }

      &:last-child {
        text-align: right;
      }
    }
  }

  &__main-col + .story__main-col {
    margin-top: 30px;

    @include media(">desktop"){
      margin: 0;
    }
  }

  &__media {
    position: relative;
    text-align: center;
    margin-top: 30px;
  }

  &__media-img {
    display: block;
    margin-bottom: -80px;
    position: relative;
    z-index: 10;
    height: 573px;

    @include media(">=tablet", "<=desktop") {
      max-width: 270px;
      margin-left: auto;
      margin-right: auto;
      height: 720px;
    }

    @include media(">desktop") {
      margin-bottom: -100px;
      height: 800px;
    }
  }

  &__media-bg {
    max-width: 1920px;
    margin: 0 auto;
    position: absolute;
    top: 0;
    bottom: 20px;
    left: 0;
    right: 0;
    width: 100%;
    overflow: hidden;

    @include media(">desktop") {
      bottom: 30px;
    }

    @include media(">desktop-xxl") {
      &:before, &:after {
        content: '';
        position: absolute;
        z-index: 5;
        bottom: 70px;
        width: 500px;
        height: 350px;
      }

      &:before {
        left: 0;
        background: linear-gradient(to left, rgba(#EAE1CF, 0), #EAE1CF);
      }

      &:after {
        right: 0;
        background: linear-gradient(to right, rgba(#EAE1CF, 0), #EAE1CF);
      }
    }
  }

  &__media-bg-img {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 0);
    max-width: none;
    width: calc(768px + 50px);
    margin-left: 50px;

    @include media(">=tablet") {
      width: calc(1024px + 50px);
      margin-left: 50px;
    }

    @include media(">=desktop") {
      bottom: -100px;
      width: calc(1920px + 50px);
      margin-left: 50px;
    }
  }

  &__media-clouds {
    position: absolute;
    top: 20px;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
    overflow: hidden;
  }

  &__media-cloud {
    width: 250px;
    margin: 0 auto;

    @include media(">=tablet") {
      width: auto;
      position: absolute;
      top: 180px;

      &:nth-child(1){
        left: 0;
        transform: translate(-70%, 0);
      }

      &:nth-child(2){
        top: 0;
        width: 350px;
        left: 50%;
        transform: translate(-50%, 0);
      }

      &:nth-child(3){
        right: 0;
        transform: translate(75%, 0);
      }
    }

    @include media(">desktop-m") {
      top: 200px;

      &:nth-child(1){
        transform: translate(-50%, 0);
      }

      &:nth-child(3){
        transform: translate(50%, 0);
      }
    }

    @include media(">desktop-xl") {
      &:nth-child(1){
        transform: translate(-20%, 0);
      }

      &:nth-child(3){
        transform: translate(20%, 0);
      }
    }

    @include media(">desktop-xxl") {
      &:nth-child(1){
        transform: translate(20px, 0);
      }

      &:nth-child(3){
        transform: translate(-20px, 0);
      }
    }
  }
}
