@charset "UTF-8";
/* Base */
/* REM calc */
/* Variables */
@font-face {
  font-family: "salden-caps";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/salden-caps.woff2") format("woff2"), url("../fonts/salden-caps.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: "salden-roman";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/salden-roman.woff2") format("woff2"), url("../fonts/salden-roman.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: "sofia-pro-regular";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/sofia-pro-regular.woff2") format("woff2"), url("../fonts/sofia-pro-regular.woff") format("woff");
  font-display: swap;
}

/* Main */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-family: "salden-roman", sans, arial;
  font-size: 16px;
  line-height: 1.4;
  color: #4a3024;
  text-align: left;
  position: relative;
  min-height: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  overflow-x: hidden;
}

body {
  margin: 0;
  text-rendering: optimizeLegibility;
  text-rendering: geometricPrecision;
  font-smooth: always;
  font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
}

.wrapword {
  word-wrap: break-word;
}

::selection {
  background: #F1DF9F;
  color: #fff;
}

:focus {
  outline: 0px;
}

img {
  border: 0;
  max-width: 100%;
  vertical-align: bottom;
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

article, aside, details, figcaption, figure, footer, header, hgroup, main, menu, nav, section, summary {
  display: block;
}

button, input, optgroup, select, textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

optgroup {
  font-weight: bold;
}

input, button {
  border: 0;
  border-radius: 0;
}

input[type="submit"] {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input:focus, button:focus, textarea:focus {
  outline: 0;
}

button {
  overflow: visible;
  text-transform: none;
}

select {
  text-transform: none;
}

textarea {
  overflow: auto;
  border: 0px;
}

a[href^="tel"],
a[href^="sms"] {
  text-decoration: none;
  cursor: default;
}

a[href^="tel"]:before,
a[href^="sms"]:before {
  display: none;
}

/* Text */
h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  font-size: 16px;
  padding: 0;
  margin: 0;
}

b, .b, strong, .strong {
  font-weight: bold;
}

em, .em {
  font-style: italic;
}

u, .u {
  text-decoration: underline;
}

a, .a {
  color: #F1DF9F;
  text-decoration: underline;
  cursor: pointer;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:hover, .a:hover {
  text-decoration: none;
}

/* Automatically generated scss for svg icons
- https://www.npmjs.com/package/svg-sprite
*/
.icon--drink-wisely {
  width: 110px;
  height: 35px;
}

.icon--title:left {
  width: 78.062px;
  height: 36.44px;
}

.icon--title:right {
  width: 78.06px;
  height: 36.44px;
}

/* Components */
.site {
  background-color: #F6F1E3;
  overflow: hidden;
}

.site__bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 450;
}

.site__in {
  position: relative;
  z-index: 500;
}

/* container
-------------------------------------------------------- */
.container {
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

@media (min-width: 768px) and (max-width: 1024px) {
  .container {
    max-width: 800px;
  }
}

.container--wide {
  max-width: 1095px;
}

@media (min-width: 768px) and (max-width: 1024px) {
  .container--wide {
    max-width: 860px;
  }
}

.footer {
  background-color: #fff;
  padding: 30px 0;
  margin-bottom: 10px;
}

@media (min-width: 768px) {
  .footer {
    padding: 50px 0;
    margin-bottom: 0;
  }
}

@media (min-width: 1025px) {
  .footer__container {
    display: flex;
    justify-content: space-between;
  }
}

.footer__text {
  font-family: "sofia-pro-regular", sans, arial;
  font-size: 12px;
  letter-spacing: 0.3px;
  margin-bottom: 30px;
  text-align: center;
}

.footer__text a {
  color: currentColor;
  text-decoration: underline;
}

@media (min-width: 768px) {
  .footer__text {
    font-size: 14px;
    line-height: 1.5;
  }
}

@media (min-width: 1025px) {
  .footer__text {
    text-align: left;
    padding-right: 50px;
    margin-bottom: 0;
  }
}

.footer__info {
  line-height: 1;
  text-align: center;
}

@media (min-width: 1025px) {
  .footer__info {
    text-align: right;
    flex-shrink: 0;
  }
}

.footer__info-wiselytext {
  text-transform: uppercase;
  font-size: 29px;
  letter-spacing: 1px;
  font-family: "salden-caps", sans, arial;
}

@media (min-width: 768px) {
  .footer__info-wiselytext {
    font-size: 34px;
  }
}

.footer__info-wiselylink {
  letter-spacing: 1.5px;
  font-size: 22px;
  color: #4a3024;
}

.footer__info-wiselylink a {
  color: currentColor;
  text-decoration: none;
}

@media (min-width: 1025px) {
  .footer__info-wiselylink a {
    transition: .3s ease color;
  }
  .footer__info-wiselylink a:hover {
    color: #B29660;
  }
}

@media (min-width: 768px) {
  .footer__info-wiselylink {
    font-size: 26px;
  }
}

.footer__info-icons {
  margin-top: 10px;
}

@media (min-width: 768px) {
  .footer__info-icons .icon--drink-wisely {
    width: 120px;
    height: 43px;
  }
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
}

@media (min-width: 1025px) {
  .nav {
    margin-top: 45px;
    max-width: 650px;
    margin: 45px auto 0;
  }
}

.nav__item {
  text-align: center;
  font-family: "salden-caps", sans, arial;
  height: 26px;
  color: #4a3024;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 1.1;
  width: calc(50% - 1px);
}

.nav__item-subtitle {
  text-transform: none;
  font-family: "salden-roman", sans, arial;
  font-size: 14px;
}

@media (min-width: 768px) {
  .nav__item {
    font-size: 20px;
  }
  .nav__item-subtitle {
    font-size: 18px;
  }
}

@media (min-width: 1025px) {
  .nav__item {
    font-size: 26px;
    transition: .3s ease color;
  }
  .nav__item-subtitle {
    font-size: 24px;
    letter-spacing: 2px;
  }
  .nav__item:hover {
    color: #B29660;
  }
}

.nav__separator {
  width: 2px;
  height: 26px;
  background-color: #4a3024;
  margin: 0;
  display: inline-block;
  pointer-events: none;
  position: relative;
  bottom: 10px;
}

@media (min-width: 768px) {
  .nav__separator {
    margin: 0 25px;
    bottom: 0;
  }
}

@media (min-width: 1025px) {
  .nav__separator {
    margin: 0 40px;
    height: 34px;
  }
}

.hero {
  text-align: center;
}

.hero__img {
  display: block;
  margin: 100px auto 30px;
}

@media (min-width: 768px) {
  .hero__img {
    margin: 150px auto 40px;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .hero__img {
    max-width: 560px;
  }
}

.hero__btns {
  display: flex;
  flex-direction: column;
  width: 90%;
  max-width: 320px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .hero__btns {
    flex-direction: row;
    max-width: none;
    justify-content: center;
  }
}

.hero__btns-item {
  position: relative;
  width: 100%;
  text-decoration: none;
  transition: .3s ease transform;
}

.hero__btns-item-in {
  position: relative;
  display: block;
  width: 100%;
  background-color: #D5C5AC;
  color: #4a3024;
  font-size: 16px;
  padding: 21px 10px 19px;
  border-radius: 4px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-family: "salden-caps", sans, arial;
  z-index: 5;
  transition: .3s ease background, .3s ease color;
}

.hero__btns-item:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  height: 8px;
  background-color: #F5E7C3;
  border-radius: 0 0 4px 4px;
  margin-top: -3px;
  z-index: 2;
}

@media (min-width: 768px) {
  .hero__btns-item {
    max-width: 370px;
  }
}

@media (min-width: 1024px) {
  .hero__btns-item-in {
    font-size: 21px;
  }
  .hero__btns-item:hover {
    transform: translate(0, -3px);
  }
  .hero__btns-item:hover .hero__btns-item-in {
    background-color: #dcc9ab;
    color: #271913;
  }
}

.hero__btns-item + .hero__btns-item {
  margin-top: 7px;
}

@media (min-width: 768px) {
  .hero__btns-item + .hero__btns-item {
    margin-top: 0;
    margin-left: 15px;
  }
}

@media (min-width: 1024px) {
  .hero__btns-item + .hero__btns-item {
    margin-left: 25px;
  }
}

.hero__scrollbtn {
  display: inline-block;
  font-family: "salden-caps", sans, arial;
  background: none;
  text-decoration: none;
  text-transform: uppercase;
  color: #4a3024;
  padding: 0;
  margin: 60px 0 0 0;
  letter-spacing: 1px;
}

.hero__scrollbtn:before, .hero__scrollbtn:after {
  content: '';
  width: 2px;
  height: 20px;
  background-color: currentColor;
  margin: 0 auto;
  display: block;
}

.hero__scrollbtn:before {
  margin-bottom: 10px;
}

.hero__scrollbtn:after {
  margin-top: 10px;
  height: 40px;
}

@media (min-width: 1025px) {
  .hero__scrollbtn {
    transition: .3s ease color;
  }
  .hero__scrollbtn:before, .hero__scrollbtn:after {
    transition: .3s ease height;
  }
  .hero__scrollbtn:hover {
    cursor: pointer;
    color: #B29660;
  }
  .hero__scrollbtn:hover:before {
    height: 50px;
  }
  .hero__scrollbtn:hover:after {
    height: 10px;
  }
}

.title {
  margin: 0;
}

.title--main {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "salden-caps", sans, arial;
  font-size: 36px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #4a3024;
  font-weight: normal;
}

.title--main .icon {
  flex-shrink: 0;
  position: relative;
  bottom: 2px;
}

.title--main span {
  margin: 0 10px;
}

.title--main .icon--title-left,
.title--main .icon--title-right {
  width: 39px;
  height: 36px;
}

@media (min-width: 768px) {
  .title--main {
    font-size: 60px;
  }
  .title--main span {
    margin: 0 15px;
  }
  .title--main .icon--title-left,
  .title--main .icon--title-right {
    width: 54px;
    height: 50px;
  }
}

@media (min-width: 1025px) {
  .title--main {
    font-size: 72px;
    letter-spacing: 3px;
  }
  .title--main .icon--title-left,
  .title--main .icon--title-right {
    width: 78px;
    height: 36px;
  }
}

.story__header {
  margin: 40px 0;
}

@media (min-width: 768px) {
  .story__header {
    margin: 150px 0 40px;
  }
}

@media (min-width: 1025px) {
  .story__header {
    margin-bottom: 100px;
  }
}

.story__wrapper {
  position: relative;
}

.story__main {
  text-align: center;
