  }
}

.story__main-col + .story__main-col {
  margin-top: 30px;
}

@media (min-width: 1025px) {
  .story__main-col + .story__main-col {
    margin: 0;
  }
}

.story__media {
  position: relative;
  text-align: center;
  margin-top: 30px;
}

.story__media-img {
  display: block;
  margin-bottom: -80px;
  position: relative;
  z-index: 10;
  height: 573px;
}

@media (min-width: 768px) and (max-width: 1024px) {
  .story__media-img {
    max-width: 270px;
    margin-left: auto;
    margin-right: auto;
    height: 720px;
  }
}

@media (min-width: 1025px) {
  .story__media-img {
    margin-bottom: -100px;
    height: 800px;
  }
}

.story__media-bg {
  max-width: 1920px;
  margin: 0 auto;
  position: absolute;
  top: 0;
  bottom: 20px;
  left: 0;
  right: 0;
  width: 100%;
  overflow: hidden;
}

@media (min-width: 1025px) {
  .story__media-bg {
    bottom: 30px;
  }
}

@media (min-width: 1921px) {
  .story__media-bg:before, .story__media-bg:after {
    content: '';
    position: absolute;
    z-index: 5;
    bottom: 70px;
    width: 500px;
    height: 350px;
  }
  .story__media-bg:before {
    left: 0;
    background: linear-gradient(to left, rgba(234, 225, 207, 0), #EAE1CF);
  }
  .story__media-bg:after {
    right: 0;
    background: linear-gradient(to right, rgba(234, 225, 207, 0), #EAE1CF);
  }
}

.story__media-bg-img {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  max-width: none;
  width: calc(768px + 50px);
  margin-left: 50px;
}

@media (min-width: 768px) {
  .story__media-bg-img {
    width: calc(1024px + 50px);
    margin-left: 50px;
  }
}

@media (min-width: 1024px) {
  .story__media-bg-img {
    bottom: -100px;
    width: calc(1920px + 50px);
    margin-left: 50px;
  }
}

.story__media-clouds {
  position: absolute;
  top: 20px;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  overflow: hidden;
}

.story__media-cloud {
  width: 250px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .story__media-cloud {
    width: auto;
    position: absolute;
    top: 180px;
  }
  .story__media-cloud:nth-child(1) {
    left: 0;
    transform: translate(-70%, 0);
  }
  .story__media-cloud:nth-child(2) {
    top: 0;
    width: 350px;
    left: 50%;
    transform: translate(-50%, 0);
  }
  .story__media-cloud:nth-child(3) {
    right: 0;
    transform: translate(75%, 0);
  }
}

@media (min-width: 1201px) {
  .story__media-cloud {
    top: 200px;
  }
  .story__media-cloud:nth-child(1) {
    transform: translate(-50%, 0);
  }
  .story__media-cloud:nth-child(3) {
    transform: translate(50%, 0);
  }
}

@media (min-width: 1601px) {
  .story__media-cloud:nth-child(1) {
    transform: translate(-20%, 0);
  }
  .story__media-cloud:nth-child(3) {
    transform: translate(20%, 0);
  }
}

@media (min-width: 1921px) {
  .story__media-cloud:nth-child(1) {
    transform: translate(20px, 0);
  }
  .story__media-cloud:nth-child(3) {
    transform: translate(-20px, 0);
  }
}

.ground {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: relative;
  height: 275px;
}

@media (min-width: 1025px) {
  .ground {
    height: 350px;
  }
}

.ground__bg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: -50px;
  width: calc(100% + 50px);
  max-width: none;
  object-fit: cover;
  height: 100%;
}

@media (min-width: 1025px) {
  .ground__bg {
    left: -10px;
    width: calc(100% + 10px);
  }
}

.ground__claim {
  font-family: "salden-caps", sans, arial;
  text-align: center;
  font-size: 26px;
  margin-bottom: 60px;
  position: relative;
  z-index: 5;
}

@media (min-width: 768px) {
  .ground__claim {
    font-size: 32px;
    letter-spacing: 1px;
  }
}

@media (min-width: 1025px) {
  .ground__claim {
    font-size: 36px;
    margin-bottom: 95px;
  }
}

.hide {
  display: none !important;
}

@media (max-width: 767px) {
  .hide--mobile {
    display: none !important;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .hide--tablet {
    display: none !important;
  }
}

@media (min-width: 1024px) {
  .hide--desktop {
    display: none !important;
  }
}

.animate {
  transition: 1s ease opacity;
  opacity: 0;
}

.animate--visible {
  opacity: 1;
}

.animate--fall {
  transition: 1s ease opacity, 1s ease transform;
  transform: translate(0, -10px);
}

.animate--fall.animate--visible {
  transform: translate(0, 0);
}

.animate--delay01 {
  transition-delay: 0.1s;
}

.animate--delay02 {
  transition-delay: 0.2s;
}

.animate--delay03 {
  transition-delay: 0.3s;
}

.animate--delay04 {
  transition-delay: 0.4s;
}

.animate--delay05 {
  transition-delay: 0.5s;
}

.flycloud {
  animation: fly 6s infinite;
  animation-timing-function: linear;
}

.flycloud--delay1 {
  animation-delay: .1s;
}

.flycloud--delay2 {
  animation-delay: .2s;
}

@keyframes fly {
  0% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(5px, 5px);
  }
  50% {
    transform: translate(0, 10px);
  }
  75% {
    transform: translate(-5px, 5px);
  }
  100% {
    transform: translate(0, 0);
  }
}

/* Helpers - vždy nakonec */
