/* Variables */

$clrBlack: #000;
$clrWhite: #fff;

$c-primary: #4a3024;
$c-secondary: #F1DF9F;

$base-font-size: 16px;
$base-line-height: 1.4;

$c-bg: #F6F1E3;
$c-text: #4a3024;
$c-text-hover: #B29660;

$f-primary: 'salden-roman', sans, arial;
$f-primary-caps: 'salden-caps', sans, arial;
$f-secondary: 'sofia-pro-regular', sans, arial;

$breakpoints: (
  'phone': 320px,
  'tablet': 768px,
  'desktop': 1024px,
  'desktop-m': 1200px,
  'desktop-l': 1400px,
  'desktop-xl': 1600px,
  'desktop-xxl': 1920px
);
