.hero {
  text-align: center;

  &__img {
    display: block;
    margin: 100px auto 30px;

    @include media(">=tablet") {
      margin: 150px auto 40px;
    }

    @include media(">=tablet", "<=desktop") {
      max-width: 560px;
    }
  }

  &__btns {
    display: flex;
    flex-direction: column;
    width: 90%;
    max-width: 320px;
    margin: 0 auto;

    @include media(">=tablet") {
      flex-direction: row;
      max-width: none;
      justify-content: center;
    }

    &-item {
      position: relative;
      width: 100%;
      text-decoration: none;
      transition: .3s ease transform;

      &-in {
        position: relative;
        display: block;
        width: 100%;
        background-color: #D5C5AC;
        color: $c-text;
        font-size: 16px;
        padding: 21px 10px 19px;
        border-radius: 4px;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-family: $f-primary-caps;
        z-index: 5;
        transition: .3s ease background, .3s ease color;
      }

      &:after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 100%;
        height: 8px;
        background-color: #F5E7C3;
        border-radius: 0 0 4px 4px;
        margin-top: -3px;
        z-index: 2;
      }

      @include media(">=tablet") {
        max-width: 370px;
      }

      @include media(">=desktop") {
        &-in {
          font-size: 21px;
        }

        &:hover {
          transform: translate(0, -3px);

          .hero__btns-item-in {
            background-color: #dcc9ab;
            color: #271913;
          }
        }
      }
    }

    &-item + .hero__btns-item {
      margin-top: 7px;

      @include media(">=tablet") {
        margin-top: 0;
        margin-left: 15px;
      }

      @include media(">=desktop") {
        margin-left: 25px;
      }
    }
  }

  &__scrollbtn {
    display: inline-block;
    font-family: $f-primary-caps;
    background: none;
    text-decoration: none;
    text-transform: uppercase;
    color: $c-text;
    padding: 0;
    margin: 60px 0 0 0;
    letter-spacing: 1px;

    &:before,
    &:after {
      content: '';
      width: 2px;
      height: 20px;
      background-color: currentColor;
      margin: 0 auto;
      display: block;
    }

    &:before {
      margin-bottom: 10px;
    }

    &:after {
      margin-top: 10px;
      height: 40px;
    }

    @include media(">desktop") {
      transition: .3s ease color;

      &:before, &:after {
        transition: .3s ease height;
      }

      &:hover {
        cursor: pointer;
        color: $c-text-hover;

        &:before {
          height: 50px;
        }

        &:after {
          height: 10px;
        }
      }
    }
  }
}
