/* container
-------------------------------------------------------- */
.container {
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;

  @include media(">=tablet", "<=desktop") {
    max-width: 800px;
  }
}

.container--wide {
  max-width: 1095px;

  @include media(">=tablet", "<=desktop") {
    max-width: 860px;
  }
}
